<template>
  <div class="new-list1">
    <div class="clearfix title">
      <p class="pull-left">{{ $t('translateLang.tradeDetail.text19') }}</p>
      <p class="pull-right close" @click="isHidden = !isHidden">{{ $t('translateLang.tradeDetail.text20') }}<i class="el-icon-arrow-down"></i></p>
      <!-- <router-link class="pull-right" tag="a" :to="{path: '/order/orderTrade', query: {'currency_id': $route.query.currency_id}}" id="more-order">{{ $t('tradeDetail.myOrder.part2')  }}</router-link> -->
    </div>
      <table class="table pulbic-table" v-if="isHidden">
        <thead>
          <tr> 
              <td class="time-long">{{ $t('translateLang.tradeDetail.text21') }}</td>
              <td>{{ $t('translateLang.tradeDetail.text22') }}</td>
              <td>{{ $t('translateLang.tradeDetail.text23') }}</td>
              <td>{{ $t('tradeDetail.myOrder.part5')  }}</td>
              <td>{{ $t('tradeDetail.myOrder.part6')  }}</td>
              <td>{{ $t('translateLang.tradeDetail.text24') }}</td>
              <td>{{ $t('translateLang.tradeDetail.text25') }}</td>
              <td>{{ $t('translateLang.tradeDetail.text26') }}</td>
              <td>{{ $t('translateLang.tradeDetail.text27') }}</td>
              <td>{{ $t('translateLang.tradeDetail.text28') }}</td>
              <td style="text-align: center;">{{ $t('tradeDetail.myOrder.part9')  }}</td>
            </tr>
        </thead>
        <tbody id="realtime-trade-sell">
          <tr v-for="item in orderList" :key="item.id">
            <td>{{ item.create_time }}</td>
            <td>{{ item.currency_area }}</td>
            <td class="c-red" v-if="item.trade_type == 'sell'">{{ $t('tradeDetail.myOrder.part10')  }}</td><td class="c-green" v-else>{{ $t('tradeDetail.myOrder.part11')  }}</td>
            <td>{{ item.trade_price }}</td><td>{{ item.trade_total_num }}</td>
            <td>{{ item.trade_count_price }}</td>
            <td>{{ item.trade_success_num }}</td>
            <td>{{ item.average_price }}</td>
            <td>{{ item.trade_total_price }}</td>
            <td>{{ $t(`order.status.${item.status}`)}}</td>
            <td style="text-align:center">
              <!-- <a href="javascript:void(0)" class="cancel" data-order-id="' + orderList[i].orders_id + '" @click="handleRevoke(item.orders_id)">{{ $t('tradeDetail.myOrder.part12')  }}</a> -->
              <el-button 
                type="text"
                v-if="item.status != -1"
                @click="handleCheck(item)">
                {{ $t('translateLang.tradeDetail.text29') }}
              </el-button>
              <span v-else>--</span>
            </td>
          </tr>
          <tr v-if="orderList.length == 0"><td colspan="11" valign="top"><div class="no-record zwjl"><img src="/static/images/zwjl.png"> <p>{{ $t('tradeDetail.myOrder.part13')  }}</p></div></td></tr>
        </tbody>
      </table>
      <check-order :show.sync="dialogVisibleCheck" :data=checkData></check-order>
  </div>
</template>

<script>
import { entrustCancelOption } from '@/api/trade'
import { getDealTradeList } from '@/api/orderManage'
import CheckOrder from './CheckOrder'

export default {
  props: ['orderList'],
  components: { CheckOrder },
  data() {
    return {
      isHidden: true,
      dialogVisibleCheck: false,
      checkData: [],
      status: {
        '0': this.$t('translateLang.tradeDetail.text30'),
        '1': this.$t('translateLang.tradeDetail.text31'),
        '2': this.$t('translateLang.tradeDetail.text32'),
        '-1': this.$t('myTrade.part8') }
    }
  },
  methods: {
    handleRevoke(orders_id) {
      this.$confirm(this.$t('add.cancleOrder'), this.$t('dialog.title'), {
        confirmButtonText: this.$t('dialog.sure'),
        cancelButtonText: this.$t('dialog.cancle'),
        type: 'warning'
      }).then(() => {
        const data = {
          'currency_trade_id': this.$route.query.trade_area_id,
          'currency_id': this.$route.query.currency_id,
          'orders_id': orders_id
        }
        entrustCancelOption(data).then(res => {
          if (res.status !== 1000) {
            this.$message.error(this.$t('tradeDetail.myOrder.part14'))
            return false
          }
          this.$message.success(this.$t('tradeDetail.myOrder.part15'))
          this.$emit('refresh')
        }).catch(() => {
        })
      }).catch(() => {
      })
    },
    handleCheck({ orders_id: order_id, currency_trade_id, currency_id }) {
      getDealTradeList({
        order_id,
        currency_trade_id,
        currency_id,
        start: 1,
        legnth: 10000
      }).then(res => {
        this.dialogVisibleCheck = true
        this.checkData = res.data.order_list
      }).catch(() => {

      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .new-list1 {
    // padding: 30px 32px;
    background: #25283c;
    // margin-top: 15px;
    font-size: 12px;
    margin-bottom: 10px;
    background: #191b2b !important;
    .title{
      line-height: 40px;
      font-size: 14px;
      color: #b0b8db;
      background: #131625;
      padding:0 10px;
    }
    .pulbic-table thead tr {
        background: #2a314e;
        color: #828594;
        line-height: 1.42857143;
    }
    .pulbic-table tr td:first-child {
        padding: 0px 10px !important;
    }
    .pulbic-table tr td {
        vertical-align: middle;
        text-align: left;
        line-height: 43px;
        color: #b0b8db;
    }
    .pulbic-table {
        text-align: left;
        font-size: 12px;
        
    }
    table.pulbic-table tbody tr {
        border-bottom: 1px solid #191b2a;
    }
    .pulbic-table>tbody tr:hover{
      background: #2a314e;
    }
    table {
      border-spacing: 0;
      border-collapse: collapse;
      width: 100%;
      max-width: 100%;
      
      margin-right: 20px;
    }
    .cancel{
      display: inline-block;
      // border: 1px solid #1fa8f6;
      width: 50px;
      height: 30px;
      line-height: 30px;
      border-radius: 5px;
      color: #1fa8f6;
      text-align: center;
    }
    a.cancel:focus, a.cancel:hover{
      color: #1fa8f6;
    }
    .close{
      cursor: pointer;
    }
    .zwjl{
      padding: 50px;
      margin: 0 auto;
      display: block;
      text-align: center;
    }
    .zwjl p{
      margin-top: 18px;
      color: #adadad;
    }
    .time-long{
      width: 180px;
    }
  }
</style>



