<template>
  <div class="trade-detail">
    <div class="content-top">
      <div class="left">
        <trade></trade>
      </div>
      <div class="right">
        <div class="l">
          <trade-head :tradeInfo="headInfo" v-if="headInfo.currency_mark"></trade-head>
          <echart ref="echart"></echart>
          <div class="form-container1">
            <el-tabs v-model="isSurePrice">
              <el-tab-pane :label="$t('translateLang.tradeDetail.text60')" name="0">
                <el-row>
                  <el-col :span="12"><buy-form v-if="tradeInfo.currency_name" :tradeInfo="tradeInfo" :isLogin="isLogin" @refresh="refresh" :isSurePrice="isSurePrice"></buy-form></el-col>
                  <el-col :span="12"><sell-form v-if="tradeInfo.currency_name" :tradeInfo="tradeInfo" :isLogin="isLogin" @refresh="refresh" :isSurePrice="isSurePrice"></sell-form></el-col>
                </el-row>
              </el-tab-pane>
              <el-tab-pane :label="$t('translateLang.tradeDetail.text61')" name="1">
                <el-row>
                  <el-col :span="12"><buy-form v-if="tradeInfo.currency_name" :tradeInfo="tradeInfo" :isLogin="isLogin" @refresh="refresh" :isSurePrice="isSurePrice"></buy-form></el-col>
                  <el-col :span="12"><sell-form v-if="tradeInfo.currency_name" :tradeInfo="tradeInfo" :isLogin="isLogin" @refresh="refresh" :isSurePrice="isSurePrice"></sell-form></el-col>
                </el-row>
              </el-tab-pane>
            </el-tabs>
          </div>
        </div>
        <div class="r r-content-list">
          <div class="new-list">
            <div class="clearfix title">
              <p class="pull-left">{{ $t('translateLang.tradeDetail.text37') }}</p>
            </div>
            <new-list :plateData="plateData" :tradeInfo="tradeInfo" :headInfo="headInfo" v-if="plateData.trade_plate"></new-list>
          </div>
          <div class="new-price-table">
            <div class="clearfix title">
              <p class="pull-left">{{ $t('tradeDetail.newPriceTable.part1')  }}</p>
            </div>
            <new-price-table v-if="plateData.trade_plate" :plateData="plateData"></new-price-table>
          </div>
        </div>
        <div class="r r-content-list-small">
          <el-tabs v-model="rListChange" type="card">
            <el-tab-pane label="买卖盘" name="0">
              <new-list :plateData="plateData" :tradeInfo="tradeInfo" :headInfo="headInfo" v-if="plateData.trade_plate"></new-list>
            </el-tab-pane>
            <el-tab-pane label="实时成交" name="1">
              <new-price-table v-if="plateData.trade_plate" :plateData="plateData"></new-price-table>
            </el-tab-pane>
          </el-tabs>
        </div>

        
      </div>
    </div>
    <my-order :orderList="orderList" @refresh="refresh"></my-order>
    <history-my-order :orderList="historyOrderList" @refresh="refresh"></history-my-order>
    <coin-info :info="info" v-if="info"></coin-info>
  </div>
</template>

<script>
  import { getCurrencyTradeInfo, getUnableTradeList } from '@/api/trade'
  import { getLinkUserInfo } from '@/utils/utils'
  import { getCurrencyIntroduce } from '@/api/trade'
  import tradeHead from './components/tradeHead'
  import echart from './components/echart'
  import buyForm from './components/buyForm'
  import sellForm from './components/sellForm'
  import newList from './components/newList'
  import myOrder from './components/myOrder'
  import historyMyOrder from './components/historyMyOrder'
  import newPriceTable from './components/newPriceTable'
  import coinInfo from './components/coinInfo'
  import trade from './components/trade'

  export default {
    components: { tradeHead, echart, buyForm, sellForm, newList, myOrder, newPriceTable, coinInfo, trade, historyMyOrder },
    data() {
      return {
        trade_area_id: 0,
        currency_id: 0,
        currency_mark: '',
        currency_name: '',
        isSurePrice: 0,
        tradeInfo: {},
        isLogin: true,
        ws: null,
        tradeDataGetInterval: null,
        orderList: [],
        historyOrderList: [],
        plateData: {},
        headInfo: {},
        headInfoWs: null,
        headInfoTradeDataGetInterval: null,
        activeForm: 'first',
        info: null,
        rListChange: 0
      }
    },
    created() {
      this.init()
      this.initWebsoketLink()
      this.initHeadInfoWebsoketLink()
      this.isLogined()
      this.getUnableTradeList()
      this.getHistoryUnableTradeList()
      this.getCurrencyIntroduce(this.$route.query.currency_id)
    },
    mounted() {
      document.body.className = 'bgBlack'
    },
    beforeRouteLeave(to, from, next) {
      document.body.className = ''
      clearInterval(this.tradeDataGetInterval)
      clearInterval(this.headInfoTradeDataGetInterval)
      next()
    },
    beforeRouteUpdate(to, from, next) {
      if (this.tradeDataGetInterval) { // 如果定时器还在运行 或者直接关闭，不用判断
        clearInterval(this.tradeDataGetInterval) // 关闭
        clearInterval(this.headInfoTradeDataGetInterval)
      }
      this.ws.close()
      this.headInfoWs.close()
  
      this.init()
      this.initWebsoketLink()
      this.initHeadInfoWebsoketLink()
      this.isLogined()
      this.getUnableTradeList()
      this.getHistoryUnableTradeList()
      console.log(to)
      this.handleGetCurrencyTradeInfo1(to.query.trade_area_id, to.query.currency_id)
      this.getCurrencyIntroduce(to.query.currency_id)
      next()
    },
    beforeDestroy() {
      if (this.tradeDataGetInterval) { // 如果定时器还在运行 或者直接关闭，不用判断
        clearInterval(this.tradeDataGetInterval) // 关闭
        clearInterval(this.headInfoTradeDataGetInterval)
      }
      this.ws.close()
      this.headInfoWs.close()
    },
    methods: {
      init() {
        this.trade_area_id = this.$route.query.trade_area_id
        this.currency_id = this.$route.query.currency_id
        this.currency_mark = this.$route.query.currency_mark
        this.currency_name = this.$route.query.currency_name
        const tradeData = {
          'trade_area_id': this.$route.query.trade_area_id,
          'currency_id': this.$route.query.currency_id,
          'currency_mark': this.$route.query.currency_mark,
          'currency_name': this.$route.query.currency_name
        }
        this.$store.commit('setTradeDetail', tradeData)
        this.handleGetCurrencyTradeInfo()
      },
      getCurrencyIntroduce(currency_id) {
        const data = {
          currency_id
        }
        getCurrencyIntroduce(data).then(res => {
          if (res.status !== 1000) {
            return false
          }
          this.info = { ...res.data }
          var div = document.createElement('div')
          div.innerHTML = this.info.currency_content
          this.info.currency_content = div.innerText || div.textContent
        }).catch(() => {
        })
      },
      isLogined() {
        if (window.localStorage.getItem('link_userInfo')) {
          this.isLogin = true
        } else {
          this.isLogin = false
        }
      },
      refresh() {
        this.handleGetCurrencyTradeInfo()
        this.getUnableTradeList()
        this.getHistoryUnableTradeList()
        this.sendData()
      },
      handleGetCurrencyTradeInfo() {
        const data = {
          currency_trade_id: this.trade_area_id,
          currency_id: this.currency_id
        }
        const id = getLinkUserInfo('uid')
        if (id) {
          data.uid = id
        }
        getCurrencyTradeInfo(data).then(res => {
          if (res.status !== 1000) {
            return false
          }
          this.tradeInfo = { ...res.data }
          console.log()
        }).catch(() => {
        })
      },
      handleGetCurrencyTradeInfo1(currency_trade_id, currency_id) {
        const data = {
          currency_trade_id,
          currency_id
        }
        const id = getLinkUserInfo('uid')
        if (id) {
          data.uid = id
        }
        getCurrencyTradeInfo(data).then(res => {
          if (res.status !== 1000) {
            return false
          }
          this.tradeInfo = { ...res.data }
          console.log()
        }).catch(() => {
        })
      },
      getUnableTradeList() {
        if (!window.localStorage.getItem('link_userInfo')) {
          return false
        } else {
          const data = {
            'currency_id': this.currency_id,
            'currency_trade_id': this.trade_area_id,
            'page_num': 1,
            'status': 0
          }
          getUnableTradeList(data).then(res => {
            if (res.status !== 1000) {
              return false
            }
            this.orderList = res.data.order_list
          }).catch(() => {
          })
        }
      },
      getHistoryUnableTradeList() {
        if (!window.localStorage.getItem('link_userInfo')) {
          return false
        } else {
          const data = {
            'currency_id': this.currency_id,
            'currency_trade_id': this.trade_area_id,
            'page_num': 1,
            'status': 3
          }
          getUnableTradeList(data).then(res => {
            if (res.status !== 1000) {
              return false
            }
            this.historyOrderList = res.data.order_list
          }).catch(() => {
          })
        }
      },
      sendData() {
        const sendData = {
          'trade_area_id': this.$route.query.trade_area_id,
          'currency_id': this.$route.query.currency_id,
          'is_more_plate': 0,
          'request_type': 'plate'
        }
  
        if (this.ws.readyState === 1) {
          this.ws.send(JSON.stringify(sendData))
        }
      },
      sendHeadInfoData() {
        const sendData = {
          'trade_area_id': this.$route.query.trade_area_id,
          'currency_id': this.$route.query.currency_id,
          'is_more_plate': 0,
          'request_type': 'trade_info'
        }
  
        if (this.headInfoWs.readyState === 1) {
          this.headInfoWs.send(JSON.stringify(sendData))
        }
      },
      initHeadInfoWebsoketLink() {
        this.headInfoWs = new WebSocket(this.$store.state.tradeUrl)
        const _this = this
        this.headInfoWs.onopen = function() {
          _this.sendHeadInfoData()
          // 添加定时器，每隔一段时间向后台发送一次数据
          _this.headInfoTradeDataGetInterval = setInterval(function() {
            _this.sendHeadInfoData()
            console.log('数据已发送')
          }, 1000)
        }

        this.headInfoWs.onmessage = function(e) {
          // myChart.hideLoading()
          _this.headInfo = JSON.parse(e.data).data
          // console.log('----头部数据--', _this.headInfo)
        }
      },
      initWebsoketLink() {
        this.ws = new WebSocket(this.$store.state.tradeUrl)
        const _this = this
        this.ws.onopen = function() {
          _this.sendData()
          // 添加定时器，每隔一段时间向后台发送一次数据
          _this.tradeDataGetInterval = setInterval(function() {
            _this.sendData()
            console.log('数据已发送')
          }, 8000)
        }

        this.ws.onmessage = function(e) {
          // myChart.hideLoading()
          _this.plateData = JSON.parse(e.data).data
          if (!_this.plateData.trade_plate.trade_sell) {
            _this.plateData.trade_plate.trade_sell = []
          }
          if (!_this.plateData.trade_plate.trade_buy) {
            _this.plateData.trade_plate.trade_buy = []
          }
          console.log('----买卖盘原数据--', _this.plateData)
        }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .trade-detail {
    font-size: 12px;
    .nav-bread{
      color: #61626a;
      a{
        color: #61626a;
      }
    }
     td{
       color: #b0b8db ;
    }
    .form-container1{
      background: #191b2b;
      margin-top: 6px;
      /deep/.el-tabs__header{
        background: #131625;
        line-height: 45px;
        height: 45px;
        padding-left: 12px;  
        .el-tabs__item{
          color: #b0b8db;
          font-size: 18px;
          line-height: 45px;
        }
        .el-tabs__nav-wrap::after{
          display: none;
        }
        .el-tabs__item.is-active{
          // font-weight: bold;
        }
      }
    }
    .content-top {
      display: -webkit-box;
      -webkit-box-sizing: border-box;
      box-sizing: border-box;
      width: 100%;
      // height: 695px;
    }
    .content-top>div.left {
      width: 300px;
      height: 100%;
      border-radius: 2px;
      color: #b0b8db;
    }
    .content-top>div.right{
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-negative: 0;
        flex-shrink: 0;
        -webkit-box-flex: 1;
        -ms-flex: 1 0 auto;
        flex: 1 0 auto;
        width: calc(100% - 300px);
        .l{
          margin: 0 8px;
          -webkit-box-flex: 1;
          -ms-flex: 1 0 auto;
          flex: 1 0 auto;
          display: -webkit-box;
          display: -ms-flexbox;
          display: flex;
          -webkit-box-orient: vertical;
          -webkit-box-direction: normal;
          -ms-flex-direction: column;
          flex-direction: column;
          width: calc(100% - 700px);
          position: relative;
        }
    }
    .content-top>div.right .r {
      width: 550px;
      display: flex;
    }
    // @media screen and (min-width: 1400px) {
    //   .content-top {
    //     height: 780px;
    //   }
    // }

    // .r-content-list{
    //   .new-list{
    //     width: 250px;
    //     // margin-left: 8px;
    //     overflow: hidden;
    //   }
    //   .new-price-table{
    //     width: 290px;
    //     margin-left: 8px;
    //     overflow: hidden;
    //   }
    //   .title{
    //     line-height: 46px;
    //     font-size: 14px;
    //     color: #b0b8db;
    //     background: #131625;
    //     padding-left: 20px;
    //   }
    // }
    .r-content-list{
      .new-list{
        width: 300px;
        overflow: hidden;
      }
      .new-price-table{
        width: 300px;
        margin-left: 8px;
        overflow: hidden;
      }
      .title{
        line-height: 46px;
        font-size: 14px;
        color: #b0b8db;
        background: #131625;
        padding-left: 20px;
      }
    }
    .r-content-list-small{
      width: 300px;
      display: none !important;
    }
    .r-content-list-small{
      display: block;
      /deep/.el-tabs{
          width: 100%;
        }
        /deep/.el-tabs__header{
          margin: 0;
          border: none;
          height: 46px;
          line-height: 46px;
          background: #131625;
          .el-tabs__item{
            border: none;
            color: #b0b8db;
            .is-active{
              
            }
          }
        }
        /deep/.el-tabs__nav{
          border: none;
        }
        /deep/.el-tabs__item.is-active{
          border-top: 2px solid #1fa8f6;
          color: #b0b8db;
        }
     }

    @media screen and (max-width: 1559px) {
      .r-content-list{
        display: none !important;
      }
      .r-content-list-small{
        display: block !important;
      }
      .content-top>div.right{
        .l{
          width: calc(100% - 300px);
        }
        .r{
          width: 300px;
        }
      }
    }

}
</style>
<style lang="less">
.trade-detail {
  .el-col-8 {
      border-right: 1px solid #191b2a !important;
  }
  .el-col-8:last-child{
    border-right: none;
  }
  
   .el-table__empty-block{
      background: #191b2b;
      border: none;
    }
    th,
    td{
      background: #191b2b;
      border-bottom: none;
    }
    .new-list .zwjl{
      // display: none !important;
    }
    /deep/.el-dialog__body{
      padding: 0;
    }
}
</style>




