<template>
  <div class="kline">
    <div class="clearfix btn-group">
      <div v-if="kLineShow">
        <div class="pull-left separate">{{ $t('translateLang.tradeDetail.text10') }}</div>
        <p class="k-echart-btn pull-left" @click="kBtnChange($event)">
          <span class="active" data-type="min1">1{{ $t('translateLang.tradeDetail.text11') }}</span>
          <span data-type="min15">15{{ $t('translateLang.tradeDetail.text11') }}</span>
          <span data-type="min30">30{{ $t('translateLang.tradeDetail.text11') }}</span>
          <span data-type="hou1">1{{ $t('translateLang.tradeDetail.text12') }}</span>
          <span data-type="hou2">2{{ $t('translateLang.tradeDetail.text12') }}</span>
          <span data-type="hou4">4{{ $t('translateLang.tradeDetail.text12') }}</span>
          <span data-type="hou6">6{{ $t('translateLang.tradeDetail.text12') }}</span>
          <span data-type="hou12">12{{ $t('translateLang.tradeDetail.text12') }}</span>
          <span data-type="days">1{{ $t('translateLang.tradeDetail.text13') }}</span>
          <span data-type="week">1{{ $t('translateLang.tradeDetail.text14') }}</span>
          <span data-type="month">1{{ $t('translateLang.tradeDetail.text15') }}</span>
        </p>
      </div>
      
      <!--K线图/深度图开始-->
      <div class="echart-tap pull-right clearfix">
        <span class="pull-left active" id="echart-tap-k" @click="kbtnClick($event)">{{ $t('translateLang.tradeDetail.text17') }}</span>
        <span class="pull-right" id="echart-tap-deep" @click="deepbtnClick($event)">{{ $t('translateLang.tradeDetail.text18') }}</span>
      </div>
    </div>
    
    <k-line :data="kData"></k-line>
    <div class="deep-echart-container hidden">
      <deep-line :data="deepData" ref="deepechart" v-if="deepData"></deep-line>
    </div>
      
      <!--K线图/深度图结束-->
  </div>
</template>

<script>
import { getTradeDepthTrend } from '@/api/trade'
import kLine from './kLine'
import deepLine from './deepLine'
export default {
  components: { kLine, deepLine },
  data() {
    return {
      kEchart: null,
      deepEchart: null,
      timeType: 'min1',
      ws: null,
      kLineShow: true,
      tradeDataGetInterval: null,
      kData: {},
      deepData: {}
    }
  },
  created() {
    this.initWebsoketLink()
    this.initDeepData()
  },
  beforeRouteLeave(to, from, next) {
    clearInterval(this.tradeDataGetInterval)
    this.ws.close()
    next()
  },
  beforeDestroy() {
    if (this.tradeDataGetInterval) { // 如果定时器还在运行 或者直接关闭，不用判断
      clearInterval(this.tradeDataGetInterval) // 关闭
    }
    this.ws.close()
  },
  methods: {
    splitData(rawData) {
      var categoryData = []
      var values = []
      for (var i = 0; i < rawData.length; i++) {
        categoryData.push(rawData[i].splice(0, 1)[0])
        values.push(rawData[i])
      }
      return {
        'categoryData': categoryData,
        'values': values
      }
    },
    sendData() {
      const sendData = {
        'trade_area_id': this.$route.query.trade_area_id,
        'currency_id': this.$route.query.currency_id,
        'time_type': this.timeType,
        'request_type': 'kline'
      }
      console.log('输出值', sendData)
      if (this.ws.readyState === 1) {
        this.ws.send(JSON.stringify(sendData))
      }
    },
    kBtnChange(ev) {
      ev = ev || window.event
      var target = ev.target || ev.srcElement

      if (target.nodeName.toLowerCase() === 'span') {
        this.timeType = target.dataset.type

        this.sendData()
        const btnDom = document.querySelectorAll('.k-echart-btn span')
        let i
        for (i = 0; i < btnDom.length; i++) {
          btnDom[i].classList.remove('active')
        }
        target.classList.add('active')
      }
    },
    kbtnClick(ev) {
      var target = ev.target || ev.srcElement
      target.classList.add('active')
      document.querySelector('#echart-tap-deep').classList.remove('active')
      this.kLineShow = true
      document.querySelector('.deep-echart-container').classList.add('hidden')
      document.querySelector('.k-echart-container').classList.remove('hidden')
      document.querySelector('.k-echart-btn').classList.remove('hidden')

      // document.querySelector('#echart-tap-deep').classList.remove('active')
    },
    initDeepData() {
      const data = {
        'currency_trade_id': this.$route.query.trade_area_id,
        'currency_id': this.$route.query.currency_id
      }
      getTradeDepthTrend(data).then(res => {
        if (res.status !== 1000) {
          return false
        }
        this.deepData = { ...res.data }
      }).catch(() => {
      })
    },
    deepbtnClick(ev) {
      document.querySelector('.deep-echart-container').classList.remove('hidden')
      document.querySelector('.k-echart-container').classList.add('hidden')
      document.querySelector('.k-echart-btn').classList.add('hidden')
      var target = ev.target || ev.srcElement
      target.classList.add('active')
      document.querySelector('#echart-tap-k').classList.remove('active')
      this.$refs.deepechart.initEchart()
      this.$refs.deepechart.renderEchart()
      this.kLineShow = false
    },
    initWebsoketLink() {
      this.ws = new WebSocket(this.$store.state.tradeUrl)
      const _this = this
      this.ws.onopen = function() {
        // console.log("交易表格数据连接成功");
        _this.sendData()
        // 添加定时器，每隔一段时间向后台发送一次数据
        _this.tradeDataGetInterval = window.setInterval(function() {
          _this.sendData()
          // console.log('数据已发送');
        }, 8000)
      }

      this.ws.onmessage = function(e) {
        // myChart.hideLoading()
        var _kData = JSON.parse(e.data).data

        const data0 = _this.splitData(_kData)
        _this.kData = { ...data0 }
        //   console.log('----原数据--', _kData)
        //  console.log('----输出K线图数据---', _this.kData)
        // tradeDetail.kEchart.renderEchart(myChart, data0)
        var values = _this.kData.values
        const dataLast = values[values.length - 1]
        document.querySelector('.tooltip-open').innerHTML = dataLast[0]
        document.querySelector('.tooltip-high').innerHTML = dataLast[2]
        document.querySelector('.tooltip-low').innerHTML = dataLast[3]
        document.querySelector('.tooltip-close').innerHTML = dataLast[1]
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .kline {
    font-size: 12px;
    background: #191b2b;
    color: #61688a;
    .btn-group{
      height: 35px;
      line-height: 35px;
      border-bottom: 1px solid #0d0f17;
    }
    .k-echart-btn{
      text-align: center;
    }
    .k-echart-btn span{
      padding-right: 10px;
      cursor: pointer;
    }
    .separate{
      margin-left: 20px;
      padding-right: 25px;
    }
    .k-echart-btn span:hover
    {
      color: #1fa8f6;
      text-decoration: none;
    }
    .k-echart-btn span.active{
      color: #1fa8f6;
    }
    .echart-tap {
      text-align: center;
      width: 104px;
      margin: 0 auto;
      border-radius: 2px;
      margin-right: 20px;
      margin-top: 5px;
      position: relative;
      z-index: 9999999;
    }
    .echart-tap span
    {
      color: #61688a;
      display: block;
      line-height: 22px;
      height: 22px;
      width: 50%;
      background: #191b2a;
      cursor: pointer;
    }
    .echart-tap span.active{
      color: white;
      background: #1fa8f6;
    }
    @media screen and (max-width: 1559px) {
      .k-echart-btn span{
        padding-right: 4px;
      }
    }
  }
</style>



