<template>
  <div class="buy-form">
    <div class="title-second">{{ $t('tradeDetail.buy.part2') }} {{ tradeInfo.trade_currency_mark }}： <span class="c-green">{{ tradeInfo.currency_buy_balance }}</span></div>
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="demo-ruleForm">
      <el-form-item  prop="buy_price" class="to-cny-container" :label="$t('translateLang.tradeDetail.text1')" v-if="isSurePrice == 0" >
        <el-input v-model.trim="ruleForm.buy_price" :placeholder="$t('tradeDetail.buy.part4')" @input="changeByPrice()" :disabled="isDisable"></el-input>
        <span v-if="$i18n.locale == 'zn'" class="to-cny" :class="{ 'hidden': isToCnyHidden }"> ≈ {{ toCny }} CNY</span>
        <span v-if="$i18n.locale == 'zh-hk'" class="to-cny" :class="{ 'hidden': isToCnyHidden }"> ≈ HK$ {{ toHkb }} </span>
        <span v-if="$i18n.locale == 'en'" class="to-cny" :class="{ 'hidden': isToCnyHidden }"> ≈ $ {{ toUsb }}</span>
      </el-form-item> 
      <el-form-item  prop="buy_price" class="to-cny-container" :label="$t('translateLang.tradeDetail.text1')" v-if="isSurePrice == 1">
        <el-input v-model="tips" @input="changeByPrice()" disabled></el-input>
        <span v-if="$i18n.locale == 'zn'" class="to-cny" :class="{ 'hidden': isToCnyHidden }"> ≈ {{ toCny }} CNY</span>
        <span  v-if="$i18n.locale == 'zh-hk'" class="to-cny" :class="{ 'hidden': isToCnyHidden }"> ≈ HK$ {{ toHkb }}</span>
        <span  v-if="$i18n.locale == 'en'" class="to-cny" :class="{ 'hidden': isToCnyHidden }"> ≈ $ {{ toUsb }}</span>
      </el-form-item> 
      <el-form-item  prop="buy_num" :label="$t('translateLang.tradeDetail.text2')" >
        <el-input v-model.trim="ruleForm.buy_num"  type="number" :placeholder="$t('tradeDetail.buy.part6')"  :disabled="isDisable"></el-input>
      </el-form-item>
      <div  class="clearfix">
        <el-slider v-model="slideData" class="pull-left slider" @input.self="changeByRange()"></el-slider>
      </div>
      <div class="toComputed">
        <span style="width:60px;display:inline-block;">
          {{ $t('translateLang.tradeDetail.text3') }}
        </span>
          {{ convert }}{{ tradeInfo.trade_currency_mark }} 
        <span class="c-gray">({{ $t('tradeDetail.buy.part9') }}{{ tradeInfo.currency_buy_fee }}%)</span>
      </div>
      <!-- <el-form-item  prop="trade_password">
        <el-input v-model.trim="ruleForm.trade_password" type="password" :placeholder="$t('tradeDetail.buy.part19')" :disabled="isDisable"></el-input>
      </el-form-item> -->
      <!-- <el-form-item  prop="pass_double" v-if="parseFloat(userInfo.is_set_double_certification) != 0">
        <el-input v-model.trim="ruleForm.pass_double" :placeholder="$t('tradeDetail.buy.part20')" :disabled="isDisable"></el-input>
      </el-form-item> -->
      <el-form-item>
        <el-button @click="submitForm('ruleForm')" class="buy-btn1" :disabled="isDisable">{{ $t('tradeDetail.buy.part10') }}</el-button>
      </el-form-item>
      
    </el-form>
    <password-dialog :dialogVisible="dialogVisible" @submitData="submitData" @closeDialog="closeDialog"></password-dialog>
  </div>
</template>

<script>
  import md5 from 'js-md5'
  import { userInfo } from '@/api/user'
  import { tradeBuyOption } from '@/api/trade'
  
  import passwordDialog from './passwordDialog'
  export default {
    props: ['isLogin', 'tradeInfo', 'isSurePrice'],
    components: { passwordDialog },
    data() {
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          this.isToCnyHidden = true
          callback(new Error(this.$t('translateLang.tradeDetail.text76')))
        } else if (value === '0' || value === 0) {
          this.isToCnyHidden = true
          callback(new Error(this.$t('translateLang.tradeDetail.text77')))
        } else if (!(/^[0-9]([0-9]|\.)*$/.test(value))) {
          this.isToCnyHidden = true
          callback(new Error(this.$t('tradeDetail.buy.part13')))
        } else if (value > parseFloat(this.tradeInfo.currency_buy_balance)) {
          this.isToCnyHidden = true
          callback(new Error(this.$t('tradeDetail.buy.part14')))
        } else if (!(parseFloat(this.ruleForm.buy_price) < this.tradeInfo.last_buy_price * 1.1)) {
          this.isToCnyHidden = true
          callback(new Error(this.$t('translateLang.tradeDetail.text8')))
        } else {
          callback()
        }
      }
      var validatePass1 = (rule, value, callback) => {
        if (value === '' || value === '0' || value === 0) {
          callback(new Error(this.$t('tradeDetail.buy.part15')))
        } else if (!(/^[0-9]([0-9]|\.)*$/.test(value))) {
          callback(new Error(this.$t('tradeDetail.buy.part16')))
        } else if (value > parseFloat(this.mostBy)) {
          callback(new Error(this.$t('tradeDetail.buy.part17')))
        } else {
          callback()
        }
      }
      return {
        isToCnyHidden: false,
        trade_currency_name: '',
        isDisable: true,
        slideData: 0,
        load: 1,
        tips: this.$t('translateLang.tradeDetail.text7'),
        dialogVisible: false,
        userInfo: {},
        ruleForm: {
          buy_price: 0,
          buy_num: 0,
          trade_password: '',
          pass_double: ''
        },
        rules: {
          buy_price: [
            { validator: validatePass }
          ],
          buy_num: [
            { validator: validatePass1 }
          ]
          // trade_password: [
          //   { required: true, message: this.$t('errorTips.noTradePwd') }
          // ]
          // pass_double: [
          //   { required: true, required: true, message: this.$t('errorTips.noGoogleCode') }
          // ]
        }
      }
    },
  
    computed: {
      mostBy: function() {
        if (this.ruleForm.buy_price === 0 || this.ruleForm.buy_price === '') {
          return 0
        } else {
          console.log(this.tradeInfo.currency_buy_balance)
          console.log(this.ruleForm.buy_price)
          return Math.floor(parseFloat(this.tradeInfo.currency_buy_balance) / parseFloat(this.ruleForm.buy_price) * 100000) / 100000
        }
      },
      toCny: function() {
        return (this.tradeInfo.exchange_cny_price * this.ruleForm.buy_price).toFixed(2)
      },
      toHkb() {
        return (this.tradeInfo.exchange_hkd_price * this.ruleForm.buy_price).toFixed(2)
      },
      toUsb() {
        return (this.tradeInfo.exchange_usd_price * this.ruleForm.buy_price).toFixed(2)
      },
      convert: function() {
        return Math.floor(this.ruleForm.buy_price * this.ruleForm.buy_num * 100000) / 100000
      },
      percent: function() {
        if (this.ruleForm.buy_num / this.mostBy <= 1) {
          return (this.ruleForm.buy_num / this.mostBy).toFixed(3)
        } else {
          return 0
        }
      }
    },
    watch: {
      tradeInfo: function() {
        this.ruleForm.buy_price = this.tradeInfo.last_buy_price
      }
    },
    created() {
      this.initUserInfo()
      this.ruleForm.buy_price = this.tradeInfo.last_buy_price
      this.trade_currency_name = this.$store.state.trade_currency_name
      if (this.isLogin) {
        this.isDisable = false
      } else {
        this.isDisable = true
      }
    },
    methods: {
      closeDialog(str) {
        this[str] = false
      },
      initUserInfo() {
        if (window.localStorage.getItem('link_userInfo')) {
          userInfo().then(res => {
            if (res.status !== 1000) {
              return false
            }
            this.userInfo = res.data
            if (parseFloat(this.userInfo.is_set_double_certification) === 0) {
              this.ruleForm.pass_double = '0'
            }
          }).catch(() => {
          })
        }
      },
      submitForm(formName) {
        if (this.userInfo.status !== 1) {
          this.$message.error(this.$t('translateLang.tradeDetail.text71'))
          return false
        }
        if ((this.userInfo.is_pwdtrade_exists).toString() !== '1') {
          this.$message.error(this.$t('translateLang.tradeDetail.text72'))
          return false
        }
  
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.dialogVisible = true
          } else {
            this.changeByPrice()
            if (!(parseFloat(this.ruleForm.buy_price) < this.tradeInfo.last_buy_price * 1.1)) {
              this.isToCnyHidden = true
            }
            console.log('error submit!!')
            return false
          }
        })
      },
      submitData(trade_password) {
        const data = { ...this.ruleForm }
        data.currency_id = this.tradeInfo.currency_id
        data.trade_area_id = this.tradeInfo.trade_area_id
        data.trade_password = md5(trade_password)
        data.price_type = this.isSurePrice ? 1 : 2
  
        this.dialogVisible = false
        tradeBuyOption(data).then(res => {
          if (res.status !== 1000) {
            this.$message.error(res.msg)
            return false
          }

          this.resetForm('ruleForm')
          this.$emit('refresh')
          this.$message.success(this.$t('tradeDetail.buy.part18'))
        }).catch(() => {
        })
      },
      changeByPrice() {
        if (!this.ruleForm.buy_price) {
          this.slideData = 0
          this.ruleForm.buy_num = ''
        }
        if (/.{1}/.test(this.ruleForm.buy_price)) {
          this.isToCnyHidden = false
          if (!(/^[0-9]*.[0-9]{0,6}$/).test(this.ruleForm.buy_price)) {
            // this.ruleForm.buy_price = this.ruleForm.buy_price.match(/^[0-9]*.[0-9]{0,5}/)
            this.ruleForm.buy_price = this.ruleForm.buy_price.match(/^[0-9]*.[0-9]{0,5}/)[0]
          }
        } else {
          this.isToCnyHidden = true
        }
  
        if (!parseFloat(this.ruleForm.buy_price)) {
          this.isToCnyHidden = true
          return false
        }
  
        if (!(/^[0-9]([0-9]|\.)*$/.test(this.ruleForm.buy_price))) {
          this.isToCnyHidden = true
          return false
        }
        if (this.ruleForm.buy_price > parseFloat(this.tradeInfo.currency_buy_balance)) {
          this.isToCnyHidden = true
          return false
        }
  
        // this.ruleForm.currency_amount = (this.ruleForm.legal_money_amount / this.detailData.quote_price).toFixed(5)
      },
      changeByRange() {
        if (!this.ruleForm.buy_price) {
          this.slideData = 0
          return false
        }
        console.log(this.ruleForm.buy_price)
        const num = parseFloat(this.tradeInfo.currency_buy_balance) / parseFloat(this.ruleForm.buy_price)
        let buy_num = num * this.slideData / 100
        if (!isFinite(num)) {
          buy_num = 0
        }
        if (buy_num) { this.ruleForm.buy_num = Math.floor(buy_num * 100000) / 100000 } else {
          this.ruleForm.buy_num = 0
        }
      },
      changeByNum() {
        if (/.{1}/.test(this.ruleForm.buy_num)) {
          if (!(/^[0-9]*.[0-9]{0,2}$/).test(this.ruleForm.buy_num)) {
            // this.ruleForm.buy_price = this.ruleForm.buy_price.match(/^[0-9]*.[0-9]{0,5}/)
            this.ruleForm.buy_num = this.ruleForm.buy_num.match(/^[0-9]*.[0-9]{0,1}/)[0]
          }
        }
        if (this.percent <= 1) {
          this.slideData = parseFloat(this.percent) * 100
        } else {
          this.slideData = 0
        }
      },
      resetForm(formName) {
        this.$refs[formName].resetFields()
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .buy-form {
    font-size: 12px;
    padding: 0 13px;
    color: #61626a;
    position: relative;
    h3{
      font-size: 14px;
      margin: 7px 0;
      font-weight: normal;
    }
    .title-second{
      line-height: 28px;
      margin-bottom: 5px;
    }
    .to-cny {
      position: absolute;
      color: #fff;
      font-size: 12px;
      line-height: 30px;
      height: 30px;
      color: #61688a;
      top: 40px;
      padding-left: 15px;
    }
    .slider{
      width: 100%;
      display: inline-block;
    }
    .best{
      position: absolute;
      right: 5px;
      top: 0;
      z-index: 99;
      font-size: 12px;
      color: #fff;
    }
    .toComputed{
      margin: 10px 0 16px;
    }
    
  }
</style>
<style lang="less">
.buy-form{
  input::-webkit-input-placeholder{
        color:#61636f !important;
    }
    input::-moz-placeholder{   /* Mozilla Firefox 19+ */
        color:#61636f !important;
    }
    input:-moz-placeholder{    /* Mozilla Firefox 4 to 18 */
        color:#61636f !important;
    }
    input:-ms-input-placeholder{  /* Internet Explorer 10-11 */ 
        color:#61636f !important;
    }
    .el-input .el-input__inner{
      background-color: #191b2b;
      border-color: #303c56;
      color: #fff;
      font-size: 12px;
    }
    .c-white{
      color: white;
    }
    .c-gray{
      color: #61626a;
    }
    .to-cny-container{
      position: relative;
      margin-bottom: 30px;
    }
    .el-slider__runway{
      margin: 7px 0 20px 0;
      height: 4px !important;
      background-color: #343645 !important;
    }
    .buy-btn1,
    .buy-btn1.is-disabled
    .buy-btn1.focus, .buy-btn1:focus, .buy-btn1:hover{
      width: 100% !important;
      background: #37c769 !important;
      border: 1px solid #37c769 !important;
      color: white !important;
      // margin-bottom: 15px;
    }
  // 新增
  .el-form-item__label{
    width: 60px;
    text-align: left;
    font-size: 12px;
  }
  .el-input{
    width: calc(100% - 60px);
  }
   .to-cny{
    left: 60px;
    width: calc(100% - 75px);
  }
   .el-slider__runway.disabled .el-slider__button{
    left: 13px;
  }
  .el-form-item__error{
    left: 60px;
  }
}
</style>



